.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(
      267.58deg,
      rgba(101, 99, 255, 0.68) 0.68%,
      rgba(101, 130, 234, 0.41) 23.39%,
      rgba(101, 99, 255, 0.4) 71.31%,
      rgba(101, 99, 255, 0.76) 98.16%
    );
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    height: 20vh;
}
.footer-content{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width:70%
}
.options{
    display: flex;
    align-items: center;
    justify-content: space-evenly;

}

h4{
    padding-right: 15px;
}

.socials{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.end-credits{
    margin-top: 36px;
    text-align: center;
}
.icons{
    background: none;
    border:none;
    padding:5px;
    cursor:pointer;
}
.landing-page {
    margin-left: 50px;
    margin-right: 50px;
    background-color: white;
  }
  .landing-content{
  display: flex;
  align-items: center;
  justify-content: space-around;
  min-height: 90vh;
}
  .landing-one {
    display: flex;
    min-height: 100vh;
    justify-content: space-between;
    align-items: center;
  }
  .contact-us{
    background: #E8F1F5;
    border: 3px solid #252E56;
    border-radius: 6px;
    /* margin: 15px 0; */
    width: 100px;
    height: 40px;
    cursor: pointer;
    transition: 0.05s;
}
  .landing-image {
    width: 500px;
    height: 500px;
  }
  
  .top-picks-cards {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .col {
    flex-direction: column;
  }
  
  .row {
    display: flex;
  }
  
  .features-grid-container {
    margin: 20px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* Three columns with equal width */
    grid-gap: 20px;
    gap: 20px;
  }
  
.about-content{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 100vh;
}

.about{
  width: 75%;
  min-height: 60%;
}

.about-image{
  width: 500px;
    height: 500px;
}
  
.ReactModal__Body--open {
    overflow: hidden;
}
.ReactModal__Overlay {
    opacity: 0;
    transition: opacity 100ms ease-in-out;
}

.ReactModal__Overlay--after-open{
    opacity: 1;
}

.ReactModal__Overlay--before-close{
    opacity: 0;
}
.tempdiv-signup{
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    min-width: 100%;
}
.signupform{
    background: #E9EDFA;
    min-height: 83vh;
    min-width: 30vw;
    border-radius: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.formbox{
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    width: 100%;
}
.signup-modal{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 85%;
    height:100%;
}
.Button{
    background: #BAC6F2;
    border-radius: 3px;
    min-width: 85%;
    min-height: 56px;
    cursor: pointer;
    margin-top: 25px;
}
@media only screen and (min-width:1200px){
    .signup-modal{
        justify-content: space-between;
    }
    .tempdiv-signup{
        min-height: 78vh;
    }
}
@media only screen and (max-width:1200px){
    .signup-modal{
        justify-content: center;
    }
    .signupform{
        border:5px solid rgb(204, 204, 204);
        min-width: 40vw;
    }
}
@media only screen and (max-width:900px){
    .signupform{
        min-width:50vw;
    }
    .signup-modal{
        justify-content: center;
    }
    .image{
        padding: 0;
        margin:0;
    }
}
